.Footer-container {
    position: relative;
}
  
.Footer-container hr {
    border: 1px solid var(--lightgray);
}
.Footer-container .footer {
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 20rem;
    gap: 4rem;
}
  
.social-links {
    display: flex;
    gap: 4rem;
}
.social-links img {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
  
.logo-f img {
    width: 10rem;
}
  
.blur-f-1 {
    bottom: 0;
    right: 15%;
    width: 26rem;
    height: 12rem;
    filter: blur(200);
    background-color: red;
}
  
.blur-f-2 {
    bottom: 0;
    left: 15%;
    width: 26rem;
    height: 12rem;
    filter: blur(200);
    background-color: rgb(255, 115, 0);
}